import React from "react";
//import PropTypes from 'prop-types';
import axios from "axios";
import Movie from "../components/Movies";
import "./Home.css";
//ÌïÑÏàòÏ†ÅÏù∏ ÏöîÏÜå -> AppÏùò Ïª¥Ìè¨ÎÑåÌä∏
//reactÎäî ÏûêÎèôÏ†ÅÏúºÎ°ú class method

class Home extends React.Component {
  state = {
    isLoading: true,
    movies: [] //state ÏÑ†Ïñ∏ÏùÄ ÌïÑÏàòÍ∞Ä ÏïÑÎãò!
  };

  getMovies = async () => {
    const {data: {data:{movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json?sort_by=rating");
    console.log(movies);
    //{data: {data:{movies}}} == movies.data.data.movies

    this.setState({movies, isLoading: false});
    //({movies:movies}) == ({movies}) => stateÏùò moviesÏôÄ axoisÏùò moviesÏûÑ
  }

  componentDidMount(){
    this.getMovies();
  }

  render() {
    const {isLoading, movies} = this.state;
    return (
    <section className="container">
      {isLoading ? (
        <div className="loader">
          <span className="loader_text_"> Loading...</span>
        </div>
      ) : (
      <div className="movies">
          <h1 className="name">Movie Theater</h1>
        {movies.map(movie =>(
          <Movie 
            key={movie.id}
            id ={movie.id} 
            year={movie.year} 
            title={movie.title}
            runtime={movie.runtime} 
            summary={movie.summary} 
            poster={movie.medium_cover_image}
            genres={movie.genres} />
          ))}
      </div>
      )}
      </section>
    )}
}

//Ïª¥Ìè¨ÎÑåÌä∏Îäî htmlÏùÑ Î∞òÌôòÌïòÎäî Ìï®ÏàòÏù¥Îã§.
export default Home;
