import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({id, year, title, runtime, summary, poster, genres}) {
    return (
        <div className="movie">
            <img src={poster} alt={title} tilte={title}/>
            <div className="movie_data">
                <h3 className="movie_title">{title}</h3>
                <h5 className="movie_year">Year: {year}</h5>
                <ul className="movie_genres">Genre: {genres.map((genre, indexNum) => <li key={indexNum} className="genres_genre">{genre}</li>)}</ul>
                <h5 className="movie_runtime">Runtime: {runtime} (min)</h5>
                <p className="movie_summary">Summary: {summary.slice(0,230)}..</p>
                <hr />
            </div>
        </div>
    );
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    runtime: PropTypes.number.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default Movie;